<template>
  <div style="margin:0 auto">
    <el-table :data="domainList" style="width: 100%;">
      <el-table-column type="index" width="200">
      </el-table-column>
      <el-table-column label="ID" width="300">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.domain_id }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.domain_name }}</el-tag>
            </div>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="renameDialogVisible = true;domain_id=scope.row.domain_id"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="delDialogVisible = true;domain_id=scope.row.domain_id"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="float:left">
      <el-button type="primary" style="margin-top: 20px;" @click="addDialogVisible = true"></el-button>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="delDialogVisible" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delDialogVisible = false"> </el-button>
        <el-button type="danger" @click="deleteDomain()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="renameDialogVisible" style="width:40%;margin:0 auto">
      <el-form :model="newDomain">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="rename.name" autocomplete="off"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="renameDialogVisible = false"> </el-button>
        <el-button type="primary" @click="renameDomain()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" style="width:40%;margin:0 auto">
      <el-form :model="newDomain">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="newDomain.name" autocomplete="off"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addNewDomain()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
    inject:['reload'],
    created (){
        this.getDomainList();
    },
    data() {
        return {
        domainList:[],
        search: '',
        multipleSelection: [],
        renameDialogVisible: false,
        addDialogVisible: false,
        delDialogVisible: false,
        rename:{'name':''},
        newDomain:{name:''},
        formLabelWidth: '120px',
        domain_id:0,
        domain_id_list:[]
        };
    },
    methods: {
      // 
      async getDomainList (){
        const res = await this.$http.get('domain');
        this.domainList = res.data.data;
      },
      // 
      async addNewDomain (){
        const {data:res} = await this.$http.post('domain',this.newDomain);
        this.domainList = res.data;
        this.addDialogVisible = false;
        if (res.message==="Create domain succeed."){
            this.$message({
                showClose: true,
                message: '',
                type: 'success'
            });
        }
        else{
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
        this.reload();
      },
      // 
      async renameDomain (){
        const {data:res} = await this.$http.patch('domain/'+this.domain_id,this.rename);
        this.renameDialogVisible = false;
        if (res.message==="Updata domain succeed."){
            this.$message({
                showClose: true,
                message: '',
                type: 'success'
            });
        }
        else{
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
        this.reload();
      },
      // 
      async deleteDomain() {
        console.log(this.domain_id);
        const res= this.$http.delete('domain/'+this.domain_id);
        this.$message({
            showClose: true,
            message: '',
            type: 'success'
        });
        this.reload();
      }
    },
};
</script>
