<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img
          src="../assets/cqu1.jpg"
          alt=""
        >
      </div>
      <!--  -->
      <el-form
        label-width="80px"
        class="loginForm"
        :rules="registerFormRules"
        :model="registerForm"
        ref="registerFormRef"
      >
        <el-form-item
          label=""
          prop="mobile"
        >
          <el-input v-model="registerForm.mobile" />
        </el-form-item>
        <el-form-item
          label=""
          prop="name"
        >
          <el-input v-model="registerForm.name" />
        </el-form-item>
        <el-form-item
          label=""
          prop="password"
        >
          <el-input
            v-model="registerForm.password"
            type="password"
          />
        </el-form-item>
        <el-form-item
          label=""
          prop="password2"
        >
          <el-input
            v-model="registerForm.password2"
            type="password"
          />
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button
            type="primary"
            @click="register"
          >
            
          </el-button>
          <el-button
            type="info"
            @click="resetRegisterForm"
          >
            
          </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkphone = (rule, value, callback) => {
      // let phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/;
      if (value === '') {
        callback (new Error(''));
      } else if (!this.isCellPhone(value)) { // methods
        callback (new Error('!'));
      } else {
        callback ();
      }
    };
    // 
    var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.registerForm.password2 !== '') {
            this.$refs.registerFormRef.validateField('checkPass');
          }
          callback();
        }
      };
      // 
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.registerForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
    return {
      // 
      registerForm: {
        mobile: '',
        name: '',
        password: '',
        password2: ''
      },
      // 
      registerFormRules: {
        // 
        mobile: [
          { required: true, validator: checkphone, trigger: 'blur' }
        ],
        // 
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, validator: validatePass, trigger: 'blur' },
        ],
        // 
        password2: [
          { required: true, validator: validatePass2, trigger: 'blur' },
        ]
      }
    };
  },
  methods: {
    // 
    resetRegisterForm() {
      // console.log(this);
      this.$refs.registerFormRef.resetFields();
    },
    // 
    isCellPhone (val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    register (){
        this.$refs.registerFormRef.validate(async valid => {
        // 
        if (!valid) return;
        const result = await this.$http.post('users', this.registerForm);
        console.log(result);
        this.$router.replace('/home');
      });
    }
  }
};
</script>

