<template>
  <el-container>
    <div style="width:100%">
      <!--  -->
      <el-tabs type="border-card">
        <el-tab-pane label="">
          <el-table :data="triple_data_list">
            <el-table-column label="ID" width="180">
              <template slot-scope="scope">
                <span>{{ scope.row.data_id }}</span>
              </template>
            </el-table-column>
            <el-table-column label="" width="250">
              <template slot-scope="scope">
                <span><div style="font-weight:bold">{{ scope.row.data_name }}</div></span>
              </template>
            </el-table-column>
            <el-table-column label="" width="180">
              <template slot-scope="scope">
                <el-tag size="medium"></el-tag>
              </template>
            </el-table-column>
            <el-table-column label="" width="180">
              <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag v-if="scope.row.private==true" size="medium" type="danger"></el-tag>
                  <el-tag v-else size="medium" type="success"></el-tag>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="" style="margin-left: 10px">
              <template slot-scope="scope">
                <!-- <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block" style="margin-top:2%;margin-left:10%">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="10"
              layout="total, prev, pager, next, jumper"
              :total="totalPage">
            </el-pagination>
          </div>
        </el-tab-pane>
        <!-- raw_data -->
        <el-tab-pane label="">
          <el-divider><i class="el-icon-receiving"></i></el-divider>
          <!--  -->
          <div v-if="method=='File'" style="margin-left:25%">
              <el-form id="new_triple_data_file">
                <el-form-item label="" :label-width="formLabelWidth" style="width:500px">
                  <el-input v-model="new_triple_data_file.name" name="name" autocomplete="off"/>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                <el-select v-model="new_triple_data_file.domain_id" placeholder="">
                  <el-option v-for="item in domain_list" :key="item.domain_id" :label="item.domain_name" :value="item.domain_id"></el-option>
                </el-select>
              </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" style="width:500px">
                  <el-radio-group v-model="new_triple_data_file.private">
                    <el-radio :label="true"></el-radio>
                    <el-radio :label="false"></el-radio>
                  </el-radio-group>
                </el-form-item>
              </el-form>
              <div style="margin-left:10%">
                <el-upload
                  class="upload-demo"
                  ref="upload"
                  action
                  :http-request="confirm_add_file" 
                  :on-preview="handlePreview"
                  :on-remove="handleRemove"
                  :file-list="fileList"
                  :auto-upload="true">
                  <el-button slot="trigger" size="small" type="primary"></el-button>
                  <el-button style="margin-left:10px;" size="small" type="success" @click="submitUpload"></el-button>
                  <div slot="tip" class="el-upload__tip" style="color:red">.json/.csv</div>
                </el-upload>
              </div>
            </div>
        </el-tab-pane>
      <!-- </el-tabs> -->
      </el-tabs>
    </div>
  </el-container>
</template>

<script>
export default {
  inject:['reload'],
  created() {
    this.get_triple_data_list();
    this.getMyDomainList();
  },
  data() {
    return {
      triple_data_list: [],
      domain_list:[],
      addDialogVisible: false,
      formLabelWidth: '120px',
      opt: 'raw_data_list',
      raw_data_id: 0,
      raw_data_name: '',
      method:'File',
      file_list:[],
      file:{},
      params:{},
      new_triple_data_file:{
        name:'',
        data_type:'data',
        private:true,
        domain_id:'',
        data_info:''
      },
      form:{
        dataType:'DB',
        DBname:''
      },
      tempForm:{
        name:''
      },
      tempDialogVisible:false,
      currentPage:1,
      totalPage:0
    };
  },
  methods: {
    async get_triple_data_list() {
      const {data: res} = await this.$http.get('data?dtype=data');
      console.log(res.data)
      this.triple_data_list = res.data;
      this.totalPage = res.data.length;
    },
    // 
    async getMyDomainList() {
      const { data: res } = await this.$http.get('domain')
      this.domain_list = res.data
    },
    // 
    goBack() {
      this.opt = 'projectList';
    },
    async confirm_add_file(fileObj){
      let formData = new FormData();
      formData.set("file", fileObj.file);
      const {data:res} = await this.$http.post('upload/data', formData,
        {
          headers: {
            "Content-type": "multipart/form-data"
          }
        }
        )
      this.new_triple_data_file.data_info=res.url;
    },
    async submitUpload() {
      try{
        const res= await this.$http.post('data/',this.new_triple_data_file);
        this.$message({
            showClose: true,
            message: '',
            type: 'success'
        });
        this.reload();
      }
      catch{
        this.$message({
            showClose: true,
            message: '',
            type: 'error'
        });
      }
    },
    raw_data_detail(){
      this.opt = 'DBDetail';
    },
    handlePreview(file) {
        console.log(file);
    },
    handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
    },
    async handleDelete(row){
      try{
        const { data: res } = await this.$http.delete('data/'+row.data_id)
        this.$message({
            showClose: true,
            message: '',
            type: 'success'
        });
        this.reload();
      }
      catch{
        this.$message({
              showClose: true,
              message: '',
              type: 'error'
          });
      }
      
    }
  }
};
</script>


 