<template>
    <div>
        <!--  -->
        <!-- <div style="margin-bottom:10px">
            
            <el-select v-model="model_type" placeholder="" @change="getModellist()">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
        </div> -->
        <!--  -->
        <el-table 
        :data="model_list"
        style="width: 100%;">
            <el-table-column
            label="ID"
            width="120">
            <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.model_id }}</span>
            </template>
            </el-table-column>
            <el-table-column
            label=""
            width="180">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                    <el-tag v-if="scope.row.model_type==0" size="medium"></el-tag>
                    <el-tag v-else size="medium" type="success"></el-tag>
                </div>
            </template>
            </el-table-column>
            <el-table-column
            label=""
            width="180">
            <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.model_name }}</span>
            </template>
            </el-table-column>
            <el-table-column
            label="url"
            width="300">
            <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.model_url }}</span>
            </template>
            </el-table-column>
            <el-table-column
            label=""
            width="300">
            <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.model_description }}</span>
            </template>
            </el-table-column>
            <el-table-column label="">
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleEdit(scope.row)"></el-button>
                <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.row.model_id)"></el-button>
            </template>
            </el-table-column>
        </el-table> 
        <!--  -->
        <!-- <div class="block" style="margin-top:2%;margin-left:10%;margin-left:35%">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="total, prev, pager, next, jumper"
                :total="totalPage">
            </el-pagination>
        </div> -->
        <div>
        <el-button type="primary" style="margin-top: 20px;float:left;" @click="addDialogVisible = true">
            
        </el-button>
        </div>
        <!-- - -->
        <el-dialog title="" :visible.sync="addDialogVisible" style="width:50%;margin:0 auto">
            <el-form :model="new_model">
                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="new_model.model_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                <el-select v-model="new_model.model_type" placeholder="">
                    <el-option
                        v-for="item in new_model_type"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                </el-form-item>
                <el-form-item label="url" :label-width="formLabelWidth">
                <el-input v-model="new_model.model_url" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-radio-group v-model="new_model.model_private">
                    <el-radio :label="true"></el-radio>
                    <el-radio :label="false"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="textarea" v-model="new_model.model_description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addModel()"> </el-button>
            </div>
        </el-dialog>
        <!-- - -->
        <el-dialog title="" :visible.sync="editDialogVisible" style="width:50%;margin:0 auto">
            <el-form :model="update_model">
                <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="update_model.model_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                <el-select v-model="update_model.modle_type" placeholder="">
                    <el-option
                        v-for="item in new_model_type"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                </el-form-item>
                <el-form-item label="url" :label-width="formLabelWidth">
                <el-input v-model="update_model.model_url" autocomplete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item label="" :label-width="formLabelWidth">
                    <el-radio-group v-model="update_model.private">
                    <el-radio :label="true"></el-radio>
                    <el-radio :label="false"></el-radio>
                    </el-radio-group>
                </el-form-item> -->
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="textarea" v-model="update_model.model_description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false"> </el-button>
                <el-button type="primary" @click="confirmEdit()"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

export default {
    inject:['reload'],
    data(){
        return{
            options:[{label:'',value:'/annotation'},{label:'',value:'/fusion'},{label:'',value:''}],
            new_model_type:[{label:'',value:0},{label:'',value:1}],
            model_type:'',
            formLabelWidth:'100px',
            model_list:[],
            currentPage:1,
            totalPage:1,
            addDialogVisible:false,
            editDialogVisible:false,
            new_model:{model_type:'',model_name:'',model_url:'',model_private:true,model_description:''},
            update_model:{model_type:'',model_name:'',model_url:'',model_private:'',model_description:''},
            model_id:''
        }
    },
    created(){
        this.getModellist();
    },
    methods:{
        // 
        async getModellist(){
            const {data:res} = await this.$http.get('model'+this.model_type);
            this.model_list=res.data;
        },
        // 
        async addModel(){
            console.log(this.new_model);
            const {data:res} = await this.$http.post('model',this.new_model);
            console.log(res);
            this.addDialogVisible = false;
            if(res.message=="Create model succeed."){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
            }
            else if(res.message=="Model already exist."){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
            this.getModellist();
        },
        // 
        async handleEdit(row){
            this.editDialogVisible=true;
            this.update_model=Object.assign({}, row);
            this.model_id=row.model_id;
            delete this.update_model.model_id;
        },
        // 
        async confirmEdit(){
            console.log(this.update_model)
            try{
                var res = await this.$http.patch('model/'+this.model_id,this.update_model);
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
            }
            catch{
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
            this.editDialogVisible=false;
            this.reload();
        },
        // 
        handleDelete(model_id){
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(async() => {
                    console.log(model_id)
                    const {data:res} = await this.$http.delete('model/'+model_id);
                    console.log(res);
                    if(res.message=="Delete model succeed."){
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.getModellist();
                    }                  
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });
            },

    }
}
</script>


