  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img
          src="../assets/cqu1.jpg"
          alt=""
        >
      </div>

      <!--  -->
      <!-- <div class="avatar_box_linggongyun">
      <img
          src="../assets/linggongyun.jpg"
          alt=""
      > -->
    <!-- </div> -->
    
    
      
      <!--  -->
      <el-form
        label-width="80px"
        class="loginForm"
        :rules="loginFormRules"
        :model="loginForm"
        ref="loginFormRef"
      >
        <el-form-item label="">
          <el-input v-model="loginForm.mobile" />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="loginForm.password"
            type="password"
          />
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button
            type="primary"
            @click="login"
          >
            
          </el-button>
          <el-button
            type="info"
            @click="resetLoginForm"
          >
            
          </el-button>
        </el-form-item>
      </el-form>
      <el-link
        type="info"
        @click="goto('/register')"
        style="margin-top:20px;margin-right:10px;"
      >
        
      </el-link>
      <el-link
        type="info"
        @click="goto('/demo')"
        style="margin-top:20px;margin-right:10px;"
      >
        
      </el-link>
    </div>
    <el-alert
      v-if="error_1"
      title=""
      type="error"
      description=""
      show-icon
    />
    <el-alert
      v-if="error_2"
      title=""
      type="error"
      description=""
      show-icon
    />
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkphone = (rule, value, callback) => {
      // let phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/;
      if (value === '') {
        callback (new Error(''));
      } else if (!this.isCellPhone(value)) { // methods
        callback (new Error('!'));
      } else {
        callback ();
      }
    };
    // 
    var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.registerForm.password2 !== '') {
            this.$refs.registerFormRef.validateField('checkPass');
          }
          callback();
        }
    };
    return {
      // 
      loginForm: {
        mobile: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        mobile: [
          { required: true, validator: checkphone, trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, validator: validatePass, trigger: 'blur' },
        ]
      },
      error_1: false,
      error_2: false
    };
  },
  methods: {
    // 
    resetLoginForm() {
      // console.log(this);
      this.$refs.loginFormRef.resetFields();
    },
    // 
    isCellPhone (val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    // 
    login() {
      var that = this;
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return;
        const res = await this.$http.post('session', this.loginForm);
        console.log(res);
        if (res.status !== 200){
        }
        else if (res.data.errno === "4004"){
          this.error_1=true;
        }
        else if (res.data.errno === "4103"){
          this.error_2=true;
        }
        else{
          console.log('success');
          this.success=true;
          this.$router.push('/home');
        }
      });
    },
    goto (path) {
      this.$router.replace(path);
    },
  }
};
</script>

